import React, { Component } from 'react'
import './index.css'
import { topicBrowseRank, topicRecommend } from '../../servers/index'
import default_img from '../../assets/imgs/banner02.jpg'

// function entityToString(entity) {
//     var div = document.createElement('div');
//     div.innerHTML = entity;
//     var res = div.innerText || div.textContent;
//     return res;
// }


export default class Slide extends Component {
    constructor() {
        super()
        this.state = {
            rankList: [],
            recommendList: [],
        }
    }
    componentDidMount() {
        this.getTopicBrowseRank()
        this.getTopicRecommend()
    }
    getTopicBrowseRank() {
        topicBrowseRank().then(res => {
            if (res.code === 200) {
                this.setState({ rankList: res.data.topics })
            }
        })
    }
    getTopicRecommend() {
        topicRecommend().then(res => {
            if (res.code === 200) {
                this.setState({ recommendList: res.data.topics })
            }
        })
    }
    render() {
        const { rankList, recommendList } = this.state
        return (
            <div>
                <div className="card" style={{ marginBottom: '30px' }}>
                    <div className="card-body">
                        <div className="card-title">
                            <p className="main-title">特别推荐</p>
                            <p className="line"></p>
                        </div>
                        {recommendList.map((item, index) => {

                            return (
                                <div className="card-item" key={item.id}>
                                    <img src={item.images[0] ? item.images[0] : default_img} alt="" className="figure-img img-fluid w-100" />
                                    <div className="card-item-info">
                                        <div className="card-text info">
                                            <p className="recommend-title">{item.title}</p>
                                            <span className="read-btn"
                                                onClick={() => this.props.history.push(`/articleDetail/${item.username}/${item.id}`)}
                                            >阅读</span>
                                        </div>
                                    </div>
                                </div>
                            )
                        })}


                    </div>
                </div>

                <div className="card" style={this.props.hideRank ? { display: "none" } : { marginBottom: '30px' }}>
                    <div className="card-body">
                        <div className="card-title">
                            <p className="main-title">点击排行</p>
                            <p className="line"></p>
                        </div>
                        {rankList.map((item, index) => {

                            if (index === 0) {
                                return (
                                    <div className="card-item" key={item.id}>
                                        <img src={item.images[0] ? item.images[0] : default_img} alt="" className="figure-img img-fluid" />
                                        <div className="card-item-info-rank">
                                            <div className="info" onClick={() => this.props.history.push(`/articleDetail/${item.username}/${item.id}`)}>{item.title}</div>
                                        </div>
                                    </div>
                                )
                            } else {
                                return (
                                    <div className="card-item" key={item.id}>
                                        <div className="card-item-small">
                                            <div className="img-wrapper">
                                                <img src={item.images[0] ? item.images[0] : default_img} alt="" />
                                            </div>
                                            <div className="card-item-small-info">
                                                <div onClick={() => this.props.history.push(`/articleDetail/${item.username}/${item.id}`)} className="card-item-small-info-title">{item.title}</div>
                                                <div className="time">
                                                    {item.created_time}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            }
                        })}
                    </div>
                </div>

            </div>
        )
    }
}