import { postMehotd, getMethod, deleteMethod, putMethod } from '../utils/utils'

export const host = 'http://xiaomile.cool/v1'
// export const host = 'http://127.0.0.1:8000/v1'


export function testApi(data) {
    return getMethod(`${host}/testApi`, data);
}




//修改用户信息
export function updateUser(data){
    return putMethod(`${host}/users/${data.username}`,data)
}

//获取用户信息
export function getUser(data){
    return getMethod(`${host}/users/${data.username}`)
}

// 上传头像
export function uploadImg(data){
    return postMehotd(`${host}/users/${data.username}/avatar`,data.avatar);
}

// 注册
export function register(data){
    return postMehotd(`${host}/users`,data);
}

// 登录
export function login(data){
    return postMehotd(`${host}/token`,data);
}


//发表文章
export function submitArticle(data){
    return postMehotd(`${host}/topics`,data);
}
//修改文章
export function updateArticle(data){
    return putMethod(`${host}/topics`,data)
}

//获取文章详情
export function getArticleDetail(data,username){
    return getMethod(`${host}/topics/${username}`,data)
}

//回复留言
export function replyMessage(data,topicId,messaegId){
    return postMehotd(`${host}/messages/${topicId}/${messaegId}`,data)
}
//留言
export function submitMessage(data,topicId){
    return postMehotd(`${host}/messages/${topicId}`,data)
}

//删除回复
export function deleteReply(data){
    return deleteMethod(`${host}/messages/${data.topicId}/${data.messageId}/${data.replyId}`)
}

//删除留言
export function deleteMessage(data){
    return deleteMethod(`${host}/messages/${data.topicId}/${data.messageId}`)
}

//点赞
export function addHeart(data){
    return postMehotd(`${host}/topics/heart`,data)
}

//取消点赞
export function deleteHeart(data){
    return deleteMethod(`${host}/topics/heart`,data)
}


//用户文章列表
export function topicList(data,username){
    return getMethod(`${host}/topics/${username}`,data)
}

//删除文章
export function deleteTopcic(data,username){
    return deleteMethod(`${host}/topics/${username}`,data)
}

//首页文章列表
export function homeTopicList(data){
    return getMethod(`${host}/topics/home`,data)
}

//点击排行列表
export function topicBrowseRank(){
    return getMethod(`${host}/topics/view_rank`)
}

//推荐文章列表
export function topicRecommend(){
    return getMethod(`${host}/topics/recommend`)
}

//轮播
export function topicCarousel(){
    return getMethod(`${host}/topics/carousel`)
}

//搜索
export function searchTopic(data){
    return getMethod(`${host}/topics/search`,data)
}
