import React, { Component } from 'react'
import { Modal, Button, message } from 'antd'
import {login} from '../../servers/index'
import './index.css'

export default class Login extends Component {
    constructor() {
        super()
        this.state = {
            username: "",
            password: "",
            isDataArrive:true
        }
    }



    validate = (key, type) => {
        let { registerFormStatus } = this.state
        let flag = true
        switch (key) {
            case 'phone':
                if (
                    !/^[1][3-9][0-9]{9}$/.test(registerFormStatus[key].value) ||
                    registerFormStatus[key].value === ''
                ) {
                    registerFormStatus[key].help = `必须为一个手机号码`
                    registerFormStatus[key].status = 'error'
                    flag = false
                } else {
                    if (type === 'login') {
                        registerFormStatus[key].help = ''
                        registerFormStatus[key].status = 'success'
                    }

                }
                break
            case 'code':
                if (key === 'code' || key === 'captcha') {
                    if (registerFormStatus[key].value.length === 6) {
                        registerFormStatus[key].status = 'success'
                        registerFormStatus[key].help = ''
                    } else {
                        registerFormStatus[key].status = 'error'
                        registerFormStatus[key].help = '验证码为6位数'
                        flag = false
                    }
                }
                break
            case 'password':
                if (registerFormStatus[key].value.length >= 6) {
                    registerFormStatus[key].status = 'success'
                    registerFormStatus[key].help = ''
                } else {
                    registerFormStatus[key].status = 'error'
                    registerFormStatus[key].help = '密码不能小于6位'
                    flag = false
                }
                break
            case 'confirmPassword':
                if (
                    registerFormStatus[key].value === registerFormStatus['password'].value && registerFormStatus[key].value.length >= 6
                ) {
                    registerFormStatus[key].status = 'success'
                    registerFormStatus[key].help = ''
                } else {
                    registerFormStatus[key].status = 'error'
                    registerFormStatus[key].help = '两次密码不一致'
                    flag = false
                }
                break
            case 'all':
                if (!this.validate('phone') || !this.validate('password') || !this.validate('code') || !this.validate('confirmPassword')) {
                    flag = false
                }
                break
            default:
                break
        }
        this.setState({ registerFormStatus })
        return flag
    }



    login = () => {

        const { username,password,isDataArrive} = this.state
        if(!isDataArrive){
            return
        }
        this.setState({isDataArrive:false})
        login({ username, password }).then(res => {
            // console.log(res)
            if (res.code === 200) {
                const token = res.data.token
                localStorage.setItem("token", token)
                const userInfo = {
                    username:res.username,
                    nickname:res.data.nickname,
                    avatar:res.data.avatar
                }
                this.props.setUserInfo(userInfo)
                localStorage.setItem("userInfo", JSON.stringify(userInfo))

                this.props.loginClose()
                message.success('登录成功')
            }else{
                message.error(res.error)
            }
            this.setState({isDataArrive:true})

        })

    }





    render() {

        let { username, password } = this.state
        return (
            <Modal
                title="登录"
                width={350}
                visible={this.props.loginVisiable}
                onCancel={this.props.loginClose}
                footer={null}
            >
                <div>
                    <div className="form-item">
                        <span>账号：</span>
                        <input type="text" placeholder="请输入用户名" className="form-input" value={username}
                            onChange={(e) => this.setState({ username: e.target.value })} 
                        />
                    </div>
                    <div className="form-item">
                        <span>密码：</span>
                        <input type="password" placeholder="请输入密码" className="form-input" value={password}
                            onChange={(e) => this.setState({ password: e.target.value })}
                        />
                    </div>

                    <Button
                        type="primary"
                        block
                        style={{ marginBottom: 10 }}
                        loading={this.state.loginLoading}
                        onClick={this.login}
                    >登 录</Button>

                    <div className='login-forget'>
                        {/* <span >忘记密码？</span> */}
                      

                            <span onClick={()=>this.props.showRegister()}>注册</span>
                        
                    </div>

                </div>
            </Modal>
        )
    }
}