import React, { Component } from 'react'
import './index.css'

import { Link } from 'react-router-dom'
import { Modal, message } from 'antd'
import Login from '../Login/index'
import Register from '../Register/index'
import { meida_url } from '../../utils/utils'
import default_avatar from '../../assets/imgs/default_avatar.jpg'

export default class Header extends Component {
    constructor() {
        super()
        this.state = {
            searchText: "",
            userInfo: {
                nickname: "",
                avatar: "",
            },
            loginVisiable: false,
            registerVisiable: false,
            current: 'mail',
        }
    }
    UNSAFE_componentWillMount() {
        let userInfo = localStorage.getItem('userInfo')
        if (userInfo) {
            userInfo = JSON.parse(userInfo)
            this.setState({ userInfo })
        }

    }

    setUserInfo(userInfo) {
        this.setState({ userInfo })
    }

    logout() {
        Modal.confirm({
            title: "提示",
            content: "确定要退出登录吗？",
            cancelText: '取消',
            okText: "确定",
            onOk: () => {
                localStorage.removeItem("userInfo")
                localStorage.removeItem("token")
                window.location.href = '/'
            }
        })
    }
    changeAvatar(url) {
        let { userInfo } = this.state
        userInfo.avatar = url
        this.setState({ userInfo })
    }
    //我的文章
    toMyList() {
        const userInfo = this.state.userInfo
        if (userInfo.username) {
            this.props.history.push('/article/' + userInfo.username)
        } else {
            message.info('请先登录！')
        }
    }
    //发表文章
    toPushlish() {
        const userInfo = this.state.userInfo
        if (userInfo.username) {
            this.props.history.push('/publish')
        } else {
            message.info('请先登录！')
        }
    }

    onSearch() {
        const { searchText } = this.state
        if (searchText.trim() === "") {
            message.info('搜索内容不能为空！', 1)
            return
        }
        const url = '/search?key=' + searchText
        window.open(url, '_blank')
    }
    showRegister() {
        this.setState({ loginVisiable: false, registerVisiable: true })
    }

    render() {
        const userInfo = this.state.userInfo
        const { searchText } = this.state
        return (
            <nav className="navbar navbar-expand-lg navbar-dark bg-primary">
                <div className="container">
                    <Link className="navbar-brand" to="">小米乐</Link>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarColor01" aria-controls="navbarColor01" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>

                    <div className="collapse navbar-collapse" id="navbarColor01">
                        <ul className="navbar-nav mr-auto">
                            <li className="nav-item">
                                <Link className="nav-link" to="">首页 <span className="sr-only">(current)</span></Link>
                            </li>
                            {/* <li className="nav-item">
                                <Link className="nav-link" to="/publish">文章列表</Link>
                            </li> */}
                            <li className="nav-item" onClick={this.toMyList.bind(this)}>
                                <span className="nav-link">我的文章</span>
                            </li>
                            <li className="nav-item" onClick={this.toPushlish.bind(this)}>
                                <span className="nav-link" >发表文章</span>
                            </li>
                            <form className="form-inline " style={{ paddingBottom: 0 }}>
                                <input className="form-control mr-sm-2" type="text" placeholder="搜索" value={searchText}
                                    onChange={(e) => this.setState({ searchText: e.target.value })}
                                />
                                <div className="btn btn-secondary my-2 my-sm-0" onClick={this.onSearch.bind(this)}>搜索</div>
                            </form>
                        </ul>
                        {userInfo.nickname ?
                            <ul className="nav navbar-nav navbar-right">
                                <li className="userInfo-wrapper" onClick={() => this.props.history.push(`/userInfo/${userInfo.username}`)}>
                                    <span> <img src={userInfo.avatar ? meida_url + userInfo.avatar : default_avatar} alt="" className="avatar" /></span>
                                    <span className="nav-link" >{userInfo.nickname}</span>
                                </li>
                                <li className="nav-item" onClick={this.logout.bind(this)}>
                                    <span className="nav-link" >登出</span>
                                </li>
                            </ul> :

                            <ul className="nav navbar-nav navbar-right">
                                <li className="nav-item" onClick={() => this.setState({ loginVisiable: true })}>
                                    <span className="nav-link" >登录</span>
                                </li>
                                <li className="nav-item" onClick={() => this.setState({ registerVisiable: true })}>
                                    <span className="nav-link" >注册</span>
                                </li>
                            </ul>
                        }


                    </div>
                </div>
                <Login loginVisiable={this.state.loginVisiable}
                    loginClose={() => this.setState({ loginVisiable: false })}
                    setUserInfo={this.setUserInfo.bind(this)}
                    showRegister={this.showRegister.bind(this)}
                />
                <Register registerVisiable={this.state.registerVisiable}
                    registerClose={()=> this.setState({registerVisiable:false})}
                    setUserInfo={this.setUserInfo.bind(this)}
                />
            </nav>
        )
    }
}