import React, { Component } from 'react'
import Header from '../../components/Header'
import { getArticleDetail, replyMessage, submitMessage, deleteReply, deleteMessage, addHeart, deleteHeart } from '../../servers/index'
import './index.css'
import { Link } from 'react-router-dom'
import { meida_url ,ScrollTop} from '../../utils/utils'
import { message, Modal } from 'antd'
import Slide from '../../components/Aside'

class DetailArticle extends Component {
    constructor() {
        super()
        this.state = {
            title: "",
            nickname: "",
            create_time: "",
            heart_count: 0,
            has_heart: false,
            introduce: "",
            content: "",
            last_id: null,
            last_title: null,
            next_id: null,
            next_title: null,
            messages: [],
            messages_count: 0,
            browse_count: 0,
            message_input: "", //留言内容
            topic_id:"",
            isDataArrive:true

        }
    }
    componentDidMount() {
        const topicId = this.props.match.params.topic_id
        const username = this.props.match.params.username
        this.getArticle(topicId,username)
        ScrollTop(0,0)
    }

    getArticle(topicId,username) {
        getArticleDetail({ topic_id: topicId, }, username).then(res => {
            if (res.code === 200) {
                let { content, limit, article_type, title, introduce, nickname, last_id, last_title,id,
                    next_id, next_title, messages, messages_count, browse_count, heart_count, has_heart, created_time
                } = res.data
                messages = messages.map(item => {
                    item.showInput = false
                    item.replyContent = ""
                    return item
                })
                if (limit === 'public') {
                    limit = 0
                } else {
                    limit = 1
                }
                this.setState({
                    content, limit, article_type, title, introduce, nickname, last_id, last_title, created_time,
                    next_id, next_title, messages, messages_count, browse_count, heart_count, has_heart,topic_id:id,
                })

            }else{
                message.error(res.error)
            }
        })

    }
    UNSAFE_componentWillReceiveProps(newProps) {
        const topic_id = this.props.match.params.topic_id
        const username = this.props.match.params.username
        const new_topic_id = newProps.match.params.topic_id
        const new_username = newProps.match.params.username
        if (new_topic_id !== topic_id || username!== new_username) {
            this.getArticle(new_topic_id,new_username)
            ScrollTop(0,200)
        }

    }
    showInput(index) {
        const { messages } = this.state
        messages[index].showInput = !messages[index].showInput
        this.setState({ messages })
    }
    inputReply(e, index) {
        const { messages } = this.state
        messages[index].replyContent = e.target.value
        this.setState({ messages })
    }

    submitMessage() {
        let { message_input, messages ,isDataArrive} = this.state
        if(!isDataArrive){
            return
        }
        this.setState({isDataArrive:false})
        const topic_id = this.props.match.params.topic_id
        submitMessage({ content: message_input }, topic_id).then(res => {
            if (res.code === 200) {
                messages.push(res.data)
                message_input = ""
                this.setState({ message_input, messages })
                message.success("留言成功！")
            } else {
                message.error(res.error)
            }
            this.setState({isDataArrive:true})
        })
    }

    submitReply(index) {
        const { messages ,isDataArrive} = this.state
        if(!isDataArrive){
            return
        }
        this.setState({isDataArrive:false})
        const topic_id = this.props.match.params.topic_id
        const content = messages[index].replyContent
        const messageId = messages[index].message_id
        replyMessage({ content }, topic_id, messageId).then(res => {
            if (res.code === 200) {
                messages[index].replys.push(res.data)
                messages[index].replyContent = ""
                messages[index].showInput = false
                this.setState({ messages })
                message.success("回复成功！")
            } else {
                message.error(res.error)
            }
        })
        this.setState({isDataArrive:true})

    }

    deleteMessage(index) {
        Modal.confirm({
            title: '确定要删除吗？',
            cancelText: '取消',
            okText: "确定",
            onOk: () => {
                let { messages } = this.state
                const topic_id = this.props.match.params.topic_id
                const messageId = messages[index].message_id
                const data = {
                    topicId: topic_id,
                    messageId,
                }
                deleteMessage(data).then(res => {
                    if (res.code === 200) {
                        messages = messages.filter(item => {
                            return item.message_id !== messageId
                        })
                        this.setState({ messages })
                        message.success("删除留言成功！")
                    } else {
                        message.error(res.error)
                    }
                })
            }
        })
    }

    deleteReply(messageIndex, replyIndex) {
        Modal.confirm({
            title: '确定要删除吗？',
            cancelText: '取消',
            okText: "确定",
            onOk: () => {
                let { messages } = this.state
                const topic_id = this.props.match.params.topic_id
                const messageId = messages[messageIndex].message_id
                const replyId = messages[messageIndex].replys[replyIndex].reply_id
                const data = {
                    topicId: topic_id,
                    messageId,
                    replyId
                }
                deleteReply(data).then(res => {
                    if (res.code === 200) {
                        messages[messageIndex].replys = messages[messageIndex].replys.filter(item => {
                            return item.reply_id !== replyId
                        })
                        this.setState({ messages })
                        message.success("删除回复成功！")
                    } else {
                        message.error(res.error)
                    }
                })
            }
        })

    }

    //点赞
    addHeart() {
        const { has_heart ,isDataArrive} = this.state
        if(!isDataArrive){
            return
        }
        this.setState({isDataArrive:false})
        const topic_id = this.props.match.params.topic_id
        if (has_heart) {
            deleteHeart({ topic_id }).then(res => {
                if (res.code === 200) {
                    message.success("已取消")
                    this.setState({ has_heart: false, heart_count: this.state.heart_count - 1 })
                } else {
                    message.error(res.error)
                }
                this.setState({isDataArrive:true})
            })
        } else {
            addHeart({ topic_id }).then(res => {
                if (res.code === 200) {
                    message.success("点赞成功")
                    this.setState({ has_heart: true, heart_count: this.state.heart_count + 1 })
                } else {
                    message.error(res.error)
                }
                this.setState({isDataArrive:true})
            })
        }
    }

    render() {
        const { content, title, introduce, nickname, last_id, last_title, created_time,
            next_id, next_title, messages, browse_count, heart_count, has_heart,topic_id } = this.state
        const username = this.props.match.params.username
        let userInfo = { username: "" }
        if (localStorage.getItem('userInfo')) {
            userInfo = JSON.parse(localStorage.getItem('userInfo'))
        }
        return (
            <div>
                <Header {...this.props} />

                <div className="container">
                    <div className="public-header" style={{ marginBottom: 20 }}>
                        <span className="header-name">文章详情</span>
                        <span>不要轻易放弃。学习成长的路上，我们长路漫漫，只因学无止境。 </span>
                    </div>
                    <div className="row">
                        <div className="col-md-8" style={{ paddingRight: 0 }}>
                            <div className="card" style={{ marginBottom: 30 }}>
                                <div className="article-body">
                                    <h4 className="article-title">
                                        <span>{title}</span>
                                        {userInfo.username === username ? 
                                        <button className="btn btn-primary btn-sm" onClick={()=>this.props.history.push(`/updateArticle/${username}/${topic_id}`)}>编辑</button>
                                         : null}
                                    </h4>
                                    <p className="card-text" >
                                        <small className="text-muted">
                                            <span>{nickname}</span>
                                            <span>{created_time}</span>
                                            <span>已阅读:{browse_count}</span>
                                            <span>点赞:{heart_count}</span>
                                        </small>
                                    </p>
                                    <div className="abstract">
                                        简介： {introduce}
                                    </div>
                                    <div dangerouslySetInnerHTML={{ __html: content }}>
                                    </div>

                                    <div className="add-heart-wrapper">
                                        <button className={has_heart ? "btn btn-secondary" : "btn btn-danger"} onClick={this.addHeart.bind(this)}>
                                            {has_heart ? '已点赞！' : "点赞！"}({heart_count})
                                    </button>
                                    </div>

                                    <div style={{ marginBottom: 40 }}>
                                        <div >
                                            {last_id ? <Link to={`/articleDetail/${username}/${last_id}`}>上一篇：{last_title}</Link> : null}
                                        </div>
                                        <div>
                                            {next_id ? <Link to={`/articleDetail/${username}/${next_id}`}>下一篇：{next_title}</Link> : null}
                                        </div>
                                    </div>

                                    <div className="comment-title">
                                        文章评论
                                </div>
                                    <div>
                                        {messages.map((item, index) => {
                                            return (
                                                <div className="message-item" key={item.message_id}>
                                                    <div className="message-user-info">
                                                        <div>
                                                            <span className="message-user">{item.author} </span>
                                                            <span>@ {item.created_time} 留言：</span>
                                                        </div>
                                                        <div>
                                                            {userInfo.username === item.author_id ?
                                                                <button type="button" className="btn btn-warning btn-sm" style={{ marginRight: 20 }}
                                                                    onClick={this.deleteMessage.bind(this, index)}>删除</button> : null
                                                            }

                                                            <img src={meida_url + item.avatar} alt="" className="avatar" />
                                                        </div>

                                                    </div>

                                                    <div className="message-content">
                                                        {item.content}
                                                        {item.showInput ?
                                                            <button type="button" className="btn btn-success btn-sm reply-btn" onClick={this.showInput.bind(this, index)}>收起</button> :
                                                            <button type="button" className="btn btn-primary btn-sm reply-btn" onClick={this.showInput.bind(this, index)}>回复</button>
                                                        }

                                                    </div>
                                                    <div className="reply-wrapper">
                                                        {item.replys.map((replyItem, replyIndex) => {
                                                            return (
                                                                <div className="reply-item" key={replyIndex}>
                                                                    <div className='message-user-info'>
                                                                        <div>
                                                                            <span className="message-user">{replyItem.author} </span>
                                                                            <span>@ {replyItem.created_time} 回复：</span>
                                                                        </div>
                                                                        <div>
                                                                            {userInfo.username === replyItem.author_id ?
                                                                                <button type="button" className="btn btn-warning btn-sm" style={{ marginRight: 20 }}
                                                                                    onClick={this.deleteReply.bind(this, index, replyIndex)}>删除</button> : null
                                                                            }
                                                                            <img src={meida_url + replyItem.avatar} alt="" className="avatar" />
                                                                        </div>

                                                                    </div>
                                                                    <div className="reply-content">
                                                                        {replyItem.content}
                                                                    </div>

                                                                </div>
                                                            )
                                                        })}
                                                    </div>
                                                    {item.showInput ?
                                                        <div>
                                                            <textarea placeholder="回复" className="reply-input"
                                                                onChange={(e) => this.inputReply(e, index)} value={item.replyContent}
                                                            />
                                                            <button type="button" className="btn btn-success" onClick={this.submitReply.bind(this, index)}>提交</button>
                                                        </div>
                                                        : null}

                                                </div>
                                            )
                                        })}
                                        {messages.length === 0 ?
                                            <div style={{ textAlign: "center" }}>目前没有留言，快写下你的留言吧~</div> : null
                                        }
                                    </div>
                                    <div className="reply-title">在这添加你的留言！</div>
                                    <textarea placeholder="留言" className="reply-input"
                                        value={this.state.message_input}
                                        onChange={(e) => this.setState({ message_input: e.target.value })}
                                    />
                                    <button type="button" className="btn btn-success"
                                        onClick={this.submitMessage.bind(this)}>提交</button>

                                </div>


                            </div>
                        </div>
                        <div className="col-md-4">
                            <Slide {...this.props}/>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}

export default DetailArticle