import React, { Component } from 'react'
import { Modal, Button, message } from 'antd'
import { register } from '../../servers/index'
import './index.css'

export default class Register extends Component {
    constructor() {
        super()
        this.state = {
            username: "",
            password2: "",
            password1: "",
            email: "",
            isDataArrive:true
        }
    }

    
    register(){
        const {username,password1,password2,email,isDataArrive} = this.state
        if(!isDataArrive){
            return
        }
        this.setState({isDataArrive:false})
        const data = {
            username: username,
            password_1: password1,
            password_2: password2,
            email: email
        }
        register(data).then(res=>{
            if (res.code === 200) {
                const token = res.data.token
                localStorage.setItem("token", token)
                const userInfo = {
                    username:res.username,
                    nickname:res.username,
                    avatar:""
                }
                this.props.setUserInfo(userInfo)
                localStorage.setItem("userInfo", JSON.stringify(userInfo))
                this.props.registerClose()
                message.success('注册成功')
            }else{
                message.error(res.error)
            }
            this.setState({isDataArrive:true})
        })
    }


    render() {

        let { username, password1, password2, email } = this.state
        return (
            <Modal
                title="注册"
                width={350}
                visible={this.props.registerVisiable}
                onCancel={this.props.registerClose}
                footer={null}
            >
                <div className="register-wrapper">
                    <div className="form-item">
                        <span className="register-text">账号：</span>
                        <input type="text" placeholder="请输入用户名" className="register-form-input" value={username}
                            onChange={(e) => this.setState({ username: e.target.value })}
                        />
                    </div>
                    <div className="form-item">
                        <span className="register-text">密码：</span>
                        <input type="password" placeholder="请输入密码" className="register-form-input" value={password1}
                            onChange={(e) => this.setState({ password1: e.target.value })}
                        />
                    </div>
                    <div className="form-item">
                        <span className="register-text">密码确认：</span>
                        <input type="password" placeholder="请确定密码" className="register-form-input" value={password2}
                            onChange={(e) => this.setState({ password2: e.target.value })}
                        />
                    </div>
                    <div className="form-item">
                        <span className="register-text">邮箱：</span>
                        <input type="email" placeholder="请输入邮箱" className="register-form-input" value={email}
                            onChange={(e) => this.setState({ email: e.target.value })}
                        />
                    </div>

                    <Button
                        type="primary"
                        block
                        style={{ marginBottom: 10 }}
                       
                        onClick={this.register.bind(this)}
                    >注册</Button>

                    <div className='login-forget'>
                        {/* <span >忘记密码？</span> */}


                        {/* <span onClick={() => this.props.showRegister()}>注册</span> */}

                    </div>

                </div>
            </Modal>
        )
    }
}